import { ref } from "vue";

const MOBILE_SCREEN = 768;

export const native = ref<boolean>(false);
export const web = ref<boolean>(false);
export const android = ref<boolean>(false);
export const ios = ref<boolean>(false);
export const mobile = ref<boolean>(false);
export const desktop = ref<boolean>(false);

update();

// atualiza os indicadores de dispositivos
function update() {
  // detecta o device conforme
  const userAgent = navigator.userAgent.toLowerCase();

  // use config.xml <preference name="AppendUserAgent" value="Cordova" />
  native.value = /cordova/.test(userAgent);
  web.value = !native.value;
  android.value = /android/.test(userAgent);
  ios.value = /iphone|ipad|ipod/.test(userAgent);
  const tablet =
    /(ipad|tablet|(android(?!.*mobile))|(windows(?!.*phone)(.*touch))|kindle|playbook|silk|(puffin(?!.*(IP|AP|WP))))/.test(
      userAgent
    );
  const smallScreen = document.documentElement.clientWidth < MOBILE_SCREEN;
  mobile.value = ((android.value || ios.value) && !tablet) || smallScreen;
  desktop.value = !mobile.value;

  // adiciona na header do html atributos conforme o nome
  setHeader("native", native.value);
  setHeader("web", web.value);
  setHeader("android", android.value);
  setHeader("ios", ios.value);
  setHeader("mobile", mobile.value);
  setHeader("desktop", desktop.value);
}

function setHeader(attr: string, active: boolean): void {
  const html = document.querySelector("html");
  if (active) {
    html?.setAttribute(attr, "");
  } else {
    html?.removeAttribute(attr);
  }
}

// atualiza dispositivos no resize da tela
window.addEventListener("resize", update, false);
