import { notification } from "./notification";
import * as _ from "./underscore";

export type ErrorInfo = {
  code: number;
  message: string;
  inner?: string;
  details: string[];
};

export const errorHandler = async (value: string | ErrorInfo) => {
  // erro sem estrutura (client - via exception)
  if (_.isString(value)) {
    notification.alert(value);
  }
  // erro de infra-estrutura
  else if (value.code >= 500) {
    notification.alert(value.message, {
      details: value.details,
    });
    console.error(value);
  }
  // sem login, acesso negado ou alteração de permissões
  else if (value.code === 401 || value.code === 403 || value.code === 409) {
    await notification.alert(value.message);
    localStorage.removeItem("access_token");
    location.href = "/";
  }
  // erro padrões NBoxException
  else if (value.code === 400) {
    const options = { details: value.inner ? value.inner.split("\n") : null };

    await notification.alert(value.message, options);
  }
  // demais erros
  else {
    notification.alert(value.message);
    console.error(value);
  }
};
