import { createApp } from "vue";
import { createRouter, createWebHashHistory } from "vue-router";
import { user, tryLogin } from "./auth";
import { errorHandler } from "./error";
import * as components from "../components";
import settings, { type Settings } from "./settings";

import css from "../assets/base.css";

// Cria uma instancia do Vue e configura para os componentes
export default async function (App: any, config: Settings) {
  // cria a instancia vue
  const app = createApp(App);

  Object.assign(settings, config);

  // configura mensagem de erro padrão do Vue
  app.config.errorHandler = errorHandler;

  // configura mensagem de warning do Vue
  app.config.warnHandler = (msg) => console.warn("*VUE WARNING*: ", msg);

  // tenta fazer login
  await tryLogin();

  // inicializa sistema de rotas
  const router = createCustomRouter(settings.routes);

  // instala no vue
  app.use(router);

  // registra todos os componentes do NBox no vue como globais
  registerComponents(app);

  // registra o estilo basico do nbox
  addStyleSheet();

  return app;
}

function createCustomRouter(routes: any[]) {
  const router = createRouter({
    history: createWebHashHistory(import.meta.env.BASE_URL),
    routes,
  });

  router.beforeEach((to, from) => {
    // verifica se o path de destino esta marcado como requer autenticação
    // e se o usuário não está logado
    if (to.meta.auth && !user.value.isLoggedIn) {
      // redireciona para tela de logout do sistema
      return {
        path: settings.auth.forbiddenUrl,
        // save the location we were at to come back later
        query: { redirect: to.fullPath },
      };
    }
  });

  return router;
}

function registerComponents(app) {
  for (const name in components) {
    const component = components[name];

    // indica componente do vue
    if (component.__name) {
      app.component(name, component);
    }
  }
}

function addStyleSheet() {
  const style = document.createElement("style");
  style.type = "text/css";
  style.appendChild(document.createTextNode(css));
  document.head.insertBefore(style, document.head.firstChild);
}
