import api from "./api";

export type UploadFile = {
  filename: string;
  contentBase64: string;
};

/**
 * Envia para o servidor armazenar (no container padrão) o arquivo. Retorna um
 * array com os fileIds adicionados
 * @param files Retorno do evento "file-select" do controle BtnUpload
 */
export async function upload(files: UploadFile[]): Promise<string> {
  const result = await api("POST", "/nbox/storage", {}, files, true);

  return result as string;
}

/**
 * Solicita ao usuário permissão para fazer download do arquivo informado
 * pelo fileId
 */
export async function download(fileId: string) {
  const request = await api(
    "GET",
    "/nbox/storage/{fileId}",
    { fileId },
    null,
    true
  );

  // cria uma link e clica sobre ele, para abrir solicitação de download
  const linkSource = `data:${request.contentType};base64,${request.contentBase64}`;
  const downloadLink = document.createElement("a");
  downloadLink.href = linkSource;
  downloadLink.download = request.filename;
  downloadLink.click();
}

/**
 * Exclui um arquivo do storage pelo fileId
 */
export async function remove(fileId: string) {
  await api("DELETE", "/nbox/storage/{fileId}", { fileId }, null, true);
}
