import { type ErrorInfo } from "./error";
import settings from "./settings";
import * as _ from "./underscore";

// interface de erro que retorna do server
export interface ApiError {
  code: number;
  message: string;
  source: string;
  stackTrace: string;
  inner?: string;
  trackId: string;
}

export default async function <T>(
  method: string,
  template: string,
  routeData: any,
  body: any,
  auth: boolean
): Promise<T> {
  const path = template.replace(/{(\w+)}/g, (match) => {
    const key = match.substring(1, match.length - 1);
    const value = routeData[key];
    delete routeData[key];
    return _.isDate(value) ? _.toIsoDateString(value) : value;
  });

  const queryString = _.toQueryString(routeData);

  const headers = new Headers({
    "content-type": "application/json",
  });

  if (auth) {
    const accessToken =
      localStorage.getItem("access_token") ||
      sessionStorage.getItem("access_token");
    headers.append("Authorization", "Bearer " + accessToken);
  }

  let response: Response;

  const url = (settings.baseUrl || "") + path + queryString;

  const location = method + " " + url;

  console.log(location);

  // cria URL completa para request
  const options = {
    method,
    headers,
    body: body === null ? null : JSON.stringify(body, _.jsonFormatter),
  };

  // se a URL necessita de incluir credenciais (Windows AD)
  // verifica se é necessário incluir credenciais para a url de login (login AD)
  const includeCredential = settings.auth.enableAd
    ? template === settings.auth.loginApi
    : false;

  if (includeCredential) {
    options.credentials = "include";
  }

  // faz a chamada fetch para capturar o response
  try {
    // executa a operação de fetch
    response = await fetch(url, options);
  } catch (ex) {
    // cria erroInfo baseado no erro do XHR
    throw createErrorFromXhr(location, ex);
  }

  let result = null;

  // verifica header do response
  const contentType = response.headers.get("Content-Type");
  const isJson = (contentType?.indexOf("application/json") ?? -1) >= 0;
  const isText = (contentType?.indexOf("text/plain") ?? -1) >= 0;

  // tratando retorno JSON
  if (isJson) {
    let json = null;
    try {
      // a variavel result pode ser tanto o conteudo como o erro
      json = await response.json(); // aqui as datas estão como String (local time)
    } catch (ex) {
      // captura o conteudo do response como texto
      throw createErrorFromJson(location, ex);
    }
    result = convertObject(json); // aqui as datas estão como Date
  }

  // se o retorno for um TEXT
  else if (isText) {
    result = await response.text();
  }

  /*console.log({
    location,
    ok: response.ok,
    contentType,
    isJson,
    isText,
    result,
  });*/

  if (response.ok) {
    return result;
  } else {
    throw createErrorFromServer(location, response, result);
  }
}

// função que converte todas as Datas (em formato String) para o objeto Date
function convertObject(value: any) {
  // se o valor for string e em formado ISO-8601 => // 2022-11-29T00:00:00Z (utc)
  if (_.isDateString(value)) {
    return new Date(value.replace("Z", "")); // remove o Z final para tratar UTC como data local (o NPoco retorna em UTC)
  }

  // se for um array, copia todos os valores
  if (Array.isArray(value)) {
    const arr = [];
    for (let i = 0; i < value.length; i++) {
      arr.push(convertObject(value[i]));
    }
    return arr;
  }

  // se o valor for um objeto, copia todos os campos
  if (_.isObject(value)) {
    const obj = {};
    const keys = Object.keys(value);
    for (let i = 0; i < keys.length; i++) {
      const key = keys[i];
      obj[key] = convertObject(value[key]);
    }
    return obj;
  }
  // para demais casos, retorna o proprio valor
  return value;
}

// cria um objeto de erro ErrorInfo para quando o erro vem de problemas no fetch
function createErrorFromXhr(location: string, ex: any): ErrorInfo {
  const err: ErrorInfo = {
    code: 500, // erro de infra-estrutura
    message: ex?.message ?? ex,
    details: ["Request: " + location],
  };

  return err;
}

// cria um objeto de erro ErrorInfo para quando o erro está no parse do JSON
function createErrorFromJson(location: string, response: Response, ex: any) {
  const err: ErrorInfo = {
    code: 500,
    message: "Erro no parser JSON de retorno: " + (ex?.message ?? ex),
    details: ["Request: " + location],
  };

  return err;
}

// cria um objeto de erro ErrorInfo para quando o retorno do servidor é != 200
function createErrorFromServer(
  location: string,
  response: Response,
  error: ApiError
) {
  const err: ErrorInfo = {
    code: response.status,
    message: error.message,
    inner: error.inner,
    details: [
      "<span style='font-weight: 700'>Request:</span> " + location,
      "<span style='font-weight: 700'>Source</span>: " + error.source ??
        "[none]",
      "<span style='font-weight: 700'>TrackId</span>: " + error.trackId ??
        "[none]",
    ],
  };

  console.warn("==== StackTrace ====");
  console.warn(error.stackTrace);

  return err;
}
