import { ref } from "vue";

export const useLoading = (initState?: false) => {
  const state = ref(initState);

  return {
    loading: state,

    /**
     * Mantem o state do loading = true enquando a função async está em execução
     * @param {Function} asyncFunc
     */
    handler: async (asyncFunc: () => void): Promise<void> => {
      try {
        state.value = true;
        await asyncFunc();
      } finally {
        state.value = false;
      }
    },
  };
};
