import { remove } from "./mask";

export const isEmail = (email: string, errorMessage = "Email inválido") => {
  const match = String(email)
    .toLowerCase()
    .match(
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    );

  return match ? true : errorMessage;
};

/**
 * Verifica se os digitos informados são um CPF válido. Retorna mensagem de erro caso false
 */
export function isCpf(
  cpf: string,
  errorMessage = "O CPF informado não é válido"
) {
  const clean = remove(cpf);
  const isValid = validaCpf(clean);
  return isValid ? true : errorMessage;
}

// http://www.receita.fazenda.gov.br/aplicacoes/atcta/cpf/funcoes.js
function validaCpf(strCPF: string) {
  let Soma;
  let Resto;
  Soma = 0;
  //strCPF  = RetiraCaracteresInvalidos(strCPF,11);
  if (strCPF == "00000000000") return false;
  for (let i = 1; i <= 9; i++)
    Soma = Soma + parseInt(strCPF.substring(i - 1, i)) * (11 - i);
  Resto = (Soma * 10) % 11;
  if (Resto == 10 || Resto == 11) Resto = 0;
  if (Resto != parseInt(strCPF.substring(9, 10))) return false;
  Soma = 0;
  for (let i = 1; i <= 10; i++)
    Soma = Soma + parseInt(strCPF.substring(i - 1, i)) * (12 - i);
  Resto = (Soma * 10) % 11;
  if (Resto == 10 || Resto == 11) Resto = 0;
  if (Resto != parseInt(strCPF.substring(10, 11))) return false;
  return true;
}
