import { ref } from "vue";
import api from "./api";
import settings from "./settings";

/**
 * Objeto de usuario atual. Reativo. Utilize isLoggedIn para testar se esta logado
 */
export const user = ref({ isLoggedIn: false });

/**
 * Objeto com as permissões/roles do usuário, para testar no client: if(permissions.R_USUARIO.LIST)
 */
export const permissions = ref({});

/**
 * Estrutura de lista com o sitemap da aplicação
 */
export const sitemap = ref([]);

/**
 * Efetua um novo login confirmo corpo necessário. Retorna um objeto do tipo IUserSession
 */
export const doLogin = async (loginData: any, fgPersiste: boolean) => {
  const token = await api(
    "POST",
    settings.auth.loginApi,
    {},
    loginData,
    settings.auth.enableAd
  );

  // persiste no localStorage (fgPersiste) ou por sessão (sessionStorage)
  const storage = fgPersiste ? localStorage : sessionStorage;

  storage.setItem("access_token", token);

  // carrega o objeto /auth/me
  const me = await api("GET", settings.auth.meApi, {}, null, true);

  // copia dados de sessão do servidor para este usuário
  user.value = { ...me.user, isLoggedIn: true };
  permissions.value = me.permissions;
  sitemap.value = me.sitemap;

  return me.user;
};

/**
 * Verifica se o navegador possui o access_token no localStorage. Se possuir, tenta fazer login acessando /auth/me.
 * Retorna true se conseguir ou false por qualquer outro motivo
 */
export const tryLogin = async (): Promise<boolean> => {
  const accessToken =
    localStorage.getItem("access_token") ||
    sessionStorage.getItem("access_token");

  if (!accessToken) return false;

  try {
    // carrega o objeto /auth/me
    const me = await api("GET", settings.auth.meApi, {}, null, true);

    // copia dados de sessão do servidor para este usuário
    user.value = { ...me.user, isLoggedIn: true };
    permissions.value = me.permissions;
    sitemap.value = me.sitemap;

    return true;
  } catch (ex) {
    // remove o token para não utilizar no proxima requisição (em produção)
    // em dev, mantem, pois pode ser só o webserver que não está rodando
    if (import.meta.env.PROD) {
      localStorage.removeItem("access_token");
      sessionStorage.removeItem("access_token");
    }

    return false;
  }
};

/**
 * Limpa a sessão do usuário. Deve ser redirecionado depois para tela de login /
 */
export const doLogout = () => {
  // limpa dados de usuario e indica deslogado
  //Object.assign(user, { isLoggedIn: false } as UsuarioSessao)

  user.value = { isLoggedIn: false };

  // limpa todo o armazenamento do usuário
  localStorage.clear();
  sessionStorage.clear();
};
